<template>
  <div class="wrap">
    <v-head></v-head>
    <main class="login-main">
      <div class="main-left">
        <div class="">
          <div class="main-logo">
            <i><img src="../assets/img/PC-logo.png" alt=""></i>
          </div>
          <div class="main-form">
            <div class="form-item">
              <span class="login-label">选择证书</span>
              <select v-model="cert" class="login-inp login-sel">
                <option value="">请选择证书</option>
                <option v-for="item in certs" :value="item.cert_id">{{item.cert_name}}</option>
              </select>
            </div>
            <div class="form-item">
              <span class="login-label">证书查询类型</span>
              <select class="login-inp login-sel" v-model="checkType">
                <option v-for="item in checks" :value="item.value">{{item.text}}</option>
              </select>
            </div>
            <div class="form-item" v-show="checkType == 'cartId'">
              <span class="login-label">身份证号查询</span>
              <input v-model="cartId" class="login-inp" type="text"  @keyup="clickKeyCode($event)" placeholder="请输入18位身份证号">
            </div>
            <div class="form-item" v-show="checkType == 'certId'">
              <span class="login-label">证书编号查询</span>
              <input v-model="certId" class="login-inp" type="text"  @keyup="clickKeyCode($event)" placeholder="请输入14位证书编号">
            </div>
            <div class="form-item">
              <span class="login-label"></span>
              <input class="login-inp login-btn" type="button" @click="submit" value="查 询">
            </div>
          </div>
        </div>
      </div>
      <div class="main-right">
        <i><img src="../assets/img/PC-big.png" alt=""></i>
      </div>
    </main>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import vHead from '../components/Header'
  import vFooter from '../components/Footer'
export default {
  name: 'Login',
  components:{
    vHead,vFooter
  },
  data () {
    return {
      certs:[],
      cert:'',
      checkType:'cartId',
      cartId:'',
      certId:'',
      checks: [
        { text: '身份证号查询', value: 'cartId' },
        { text: '证书编号查询', value: 'certId' }
      ],

      user:'',
      pwd:''
    }
  },
  created(){
    this.getCerts();
  },
  methods:{
    getCerts(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/e-certs').then(res =>{
        if(res.data.success){
          self.certs = res.data.data;
        }else{

        }

      })
    },
    checkForm(){
      var self = this;
      if(!self.cert){
        self.$toast.center('请选择证书');
        return false;
      }
      if(self.checkType == 'certId'){
        console.log(self.certId);
        let reg_d = /^\d{14}$/;
        if(self.certId == ''){//test:20180901000001
          self.$toast.center('请输入证书编号');
          return false;
        }
        if(!reg_d.test(self.certId)){
          self.$toast.center('证书编号不正确');
          return false;
        }
      }
      if(self.checkType == 'cartId'){
        var reg_card = /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|[xX])$/;
        if(self.cartId == ''){
          self.$toast.center('请输入身份证号');
          return false;
        }
        if (!reg_card.test(self.cartId)) {
          self.$toast.center("请输入正确身份证号！");
          return false;
        }
      }
      return true;


    },
    clickKeyCode:function(ev){
      if(ev.keyCode == 13){
        this.submit();
      }
    },
    submit: function(){
      let self = this;
      if(!self.checkForm()){
        self.$toast.center('输入有误');
        return false;
      }
      //self.$router.push('/home');
      let param = self.checkType == 'certId'?self.certId:self.cartId;
      console.log(param)
      self.$axios.get(self.baseUrl+'/api/e-certs/'+self.cert+'/'+param).then(function(res){
        if(res.data.success){
          self.$router.push({path:'/Home',query:{cert_id:self.cert,param:param}});
        }else{
          self.$toast.center(res.data.msg);
        }
      })
    },
  }
}
</script>

<style scoped>
.wrap{
  width:100%;
  height:100%;
  /*background-color:#f6f6f6;*/
}


  .login-main{
    width:1000px;
    /*height:600px;*/
    overflow: hidden;
    margin:0 auto;
    padding-bottom:40px;
    background-color:#fff;
    border-top:2px solid #D19421;
    font-size:18px;
    color:#333;
  }
  .main-left{
    width:540px;
    margin:40px 0;
    float: left;
  }
  .main-right{
    width:450px;
    margin:40px 0 0;
    padding:20px 0;
    float:right;
    border-left:1px solid #ddd;
    text-align: center;
  }
  .main-logo{
    margin:40px auto;
    width:97px;
    height:98px;
  }
  .form-item{
    width:430px;
    margin:0 auto 20px;
  }
  .login-label{
    display: inline-block;
    width:120px;
    height:46px;
    line-height: 46px;
    text-align: right;
  }
  .login-inp{
    width:290px;
    height:46px;
    font-size:18px;
    line-height:46px;
    box-sizing: border-box;
    border:1px solid #ccc;
    padding-left:16px;
    margin-left:10px;
    border-radius: 3px;
    outline: none;
  }
  .login-inp.login-btn{
    padding:0;
    border:none;
    background-color:#D19421;
    color:#fff;
    font-size:18px;
    cursor: pointer;
  }
  .login-btn:hover{
    background-color:#E6A529;
  }
  .main-right i{
    display: inline-block;
    width:300px;
    height:435px;
  }
  .main-right i img{
    width:100%;
    height:100%;
  }

  .login-sel{
    color:#999;
    background: url("../assets/img/down.png") no-repeat 255px center;
    background-size: 20px 11px;
  }
</style>



